import React, {Component} from 'react';


class PanganLokal extends Component{
	constructor(props){
		super(props);
		this.state={
			gambar:"https://cdn.pixabay.com/photo/2015/05/26/17/42/pop-corn-785074__340.jpg",
			caption:"Jagung (Zea Mays) adalah salah satu  tanaman pangan penghasil karbohidrat yang terpenting di dunia, selain gandum dan padi. Bagi penduduk Amerika Tengah dan Selatan, bulir jagung adalah pangan pokok, sebagaimana bagi sebagian penduduk Afrika dan beberapa daerah di Indonesia. Berdasarkan temuan-temuan genetik, antropologi, dan arkeologi diketahui bahwa daerah asal jagung adalah Amerika Tengah (Meksiko bagian selatan). Budidaya jagung telah dilakukan di daerah ini 10.000 tahun yang lalu, lalu teknologi ini dibawa ke Amerika Selatan (Ekuador) sekitar 7.000 tahun yang lalu, dan mencapai daerah pegunungan di selatan Peru pada 4.000 tahun yang lalu. Hingga kini dikenal 50.000 kultivar jagung, baik yang terbentuk secara alami maupun dirakit melalui pemuliaan tanaman. Untuk di wilayah Nusantara sendiri, pada tanaman jagung diperkirakan masuk pada sekitar abad ke-16 oleh penjelajah Portugis."
		}

	}
	ubahGambarDefault(e) {
		e.preventDefault()
		this.setState({gambar: "https://cdn.pixabay.com/photo/2015/05/26/17/42/pop-corn-785074__340.jpg"})
		this.setState({caption: "Jagung (Zea Mays) adalah salah satu  tanaman pangan penghasil karbohidrat yang terpenting di dunia, selain gandum dan padi. Bagi penduduk Amerika Tengah dan Selatan, bulir jagung adalah pangan pokok, sebagaimana bagi sebagian penduduk Afrika dan beberapa daerah di Indonesia. Berdasarkan temuan-temuan genetik, antropologi, dan arkeologi diketahui bahwa daerah asal jagung adalah Amerika Tengah (Meksiko bagian selatan). Budidaya jagung telah dilakukan di daerah ini 10.000 tahun yang lalu, lalu teknologi ini dibawa ke Amerika Selatan (Ekuador) sekitar 7.000 tahun yang lalu, dan mencapai daerah pegunungan di selatan Peru pada 4.000 tahun yang lalu. Hingga kini dikenal 50.000 kultivar jagung, baik yang terbentuk secara alami maupun dirakit melalui pemuliaan tanaman. Untuk di wilayah Nusantara sendiri, pada tanaman jagung diperkirakan masuk pada sekitar abad ke-16 oleh penjelajah Portugis."})

	}
	ubahGambarSatu(e) {
		e.preventDefault()
		this.setState({gambar:"https://cdn.pixabay.com/photo/2020/06/19/21/47/sweet-potato-5318960__340.jpg"})
		this.setState({caption:"Ubi jalar adalah salah satu sumber karbohidrat yang juga sangat diminati oleh masyarakat, yang memanfaatkan akarnya yang berbentuk ubi. Berdasarkan sumber di Wikipedia tanaman ini kabarnya berasal dari Amerika Selatan tropis yang kemudian tersebar dibeberapa benua, namun hal ini juga masih menjadi kontroversi akan asal-usulnya. Tanaman ini masuk ke Indonesia dan beberapa negara Asia lainnya oleh orang Spanyol, dan oleh orang-orang Asia tidak hanya dimanfaatkan ubinya saja namun juga daun muda dari tanaman merambat ini sebagai sayuran."})

	}
	ubahGambarDua(e) {
		e.preventDefault()
		this.setState({gambar:"https://cdn.pixabay.com/photo/2020/09/17/09/25/cassava-5578528__340.jpg"})
		this.setState({caption:"Singkong (Manihot utilissima atau Manihot esculenta crantz) yang juga dikenal dengan nama Ketela Pohon atau Ubi Kayu adalah pohon tahunan tropika dan subtropika dari keluarga Euphorbiaceae. Umbinya dikenal luas sebagai makanan pokok penghasil karbohidrat dan daunnya sebagai sayuran. Di Indonesia, singkong ditanam secara komersial pada sekitar tahun 1810, setelah sebelumnya diperkenalkan orang Portugis pada abad ke-16 ke Nusantara dari Brasil melalui Kebun Raya Bogor. Pada tahun 1914-1918 saat terjadi krisis pangan di Indonesia, singkong mulai menduduki posisi pangan pokok alternatif selain beras dan jagung."})
	}
	render(){
		return(
			
			<div>
			<div class="btn-group btn-block" role="group" aria-label="Basic example">

			<button type="button" class="btn btn-secondary btn-lg" onClick={(e)=>this.ubahGambarDefault(e)}>JAGUNG</button>
			<button type="button" class="btn btn-secondary btn-lg" onClick={(e)=>this.ubahGambarSatu(e)}>UBI JALAR</button>
			<button type="button" class="btn btn-secondary btn-lg " onClick={(e)=>this.ubahGambarDua(e)}>  UBI KAYU  </button>
			</div>
			<br/>
			<br/>
			<table class="table table-dark">
			<tbody>
			<tr>
				<th scope="col">
				<td colspan="3">
					<img src={this.state.gambar} class="rounded" alt="Pemandangan"/>
				</td>
				</th>
				<th scope="row">
				<td>
					<p class="font-weight-normal" align="justify">{this.state.caption}</p>
				</td>
				</th>
			</tr>
			</tbody>
			
			</table>



			</div>			





			
			)
	}
}

export default PanganLokal;