import React, { Component } from 'react';

class Artikel extends Component {
	constructor(props){
		super(props)
		this.state = {
			artikel: "Secara umum B2SA adalah susunan makanan untuk dikonsumsi seseorang sekali makan atau untuk sehari "

		}


		this.ubahArtikelSatu = this.ubahArtikelSatu.bind(this);
		

}	
	ubahArtikelSatu(e){
		e.preventDefault()
		
		this.setState({artikel: "Secara umum B2SA adalah susunan makanan untuk dikonsumsi seseorang sekali makan atau untuk sehari menurut waktu makan (pagi, siang, dan sore/malam), yang mengandung zat gizi untuk memenuhi kebutuhan tubuh dengan jumlah yang memenuhi kaidah gizi seimbang yang sesuai dengan daya terima (selera, budaya) dan kemampuan daya beli masyarakat serta aman untuk dikonsumsi."})
	}

	handlePesan(B1, B2, S, A, e){
		e.preventDefault()

		alert(B1)
		alert(B2)
		alert(S)
		alert(A)
	}


	render(){
		return(
			<div>
			<h1 class="h1 text-body" align="left">Ayo Konsumsi Pangan B2SA!!</h1>
			<p class="text-body" align="justify">{this.state.artikel}
			<a href="/" class="text-decoration-none text-body alert-link" onClick={(e)=>this.ubahArtikelSatu(e)}> Selengkapnya...</a></p>
			<h3 class="text-body">Apa Kepanjangan dari B2SA?</h3>
			<button type="button" class="btn btn-warning btn-lg" onClick={(e)=>this.handlePesan("Makanan yang : Beragam","Makanan yang : Bergizi", "Makanan yang : Seimbang", "Makanan yang : Aman",e)}>  Click Me  </button>
			</div>
			)
		}
	}

export default Artikel;